<template>
  <div id="productlistitem">
    <div class="itemleft">
      <div class="click"
           :class="{active: isActive}"
           @click="changeItemChecked"></div>
    </div>
    <div class="itemright">
      <div class="left">
        <img :src="shopCartItem.image" alt="">
      </div>
      <div class="right">
        <p>{{shopCartItem.title}}</p>
        <p>{{shopCartItem.desc}}</p>
        <span>价格: {{shopCartItem.realPrice}}￥</span>
        <span>数量:{{shopCartItem.count}}件</span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "ProductListItem",
    props: {
      shopCartItem: {
        type: Object,
        default() {
          return {}
        }
      },
      shopCartIndex: {
        type: Number,
        default() {
          return 0;
        }
      }
    },
    computed: {
      isActive() {
        return this.shopCartItem.checked
      }
    },
    methods: {
      changeItemChecked() {
        // this.shopCartItem.clicked = !this.isActive
        const payload = {index: this.shopCartIndex, isActive: this.isActive}
        this.$store.commit('ChangeItemChecked', payload)
      }
    }
  }
</script>

