<template>
  <div id="bottombar">
    <div class="leftbutton" @click="ifSelectALL">
      <div class="check" :class="{isActive:selectAll}"></div>
      <span>全选</span>
    </div>
    <div class="center">
      <span>总价格:</span>
      <span>{{allPrice}}￥</span>
    </div>
    <div class="rightbuying" @click="paying">
      <span>确认支付({{buyingCount}})</span>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    name: "ProductBottomBar",

    computed: {
      ...mapGetters(['shopCartLength', 'shopCartList']),
      allPrice() {
        const allCheckPrd = this.shopCartList.filter(item => item.checked === true)
        let allSpend = 0
        for (let i = 0; i < allCheckPrd.length; i++) {
          allSpend = allSpend + allCheckPrd[i].realPrice * allCheckPrd[i].count
        }
        return allSpend.toFixed(2)
      },
      buyingCount() {
        const allCheckPrd = this.shopCartList.filter(item => item.checked === true)
        let allCheckPrdLength = 0
        for (let i = 0; i < allCheckPrd.length; i++) {
          allCheckPrdLength = allCheckPrdLength + 1 * allCheckPrd[i].count
        }
        return allCheckPrdLength
      },
      selectAll() {
        if (this.shopCartLength === 0) {
          return false
        } else return !this.shopCartList.find(item => item.checked === false)
      }
    },
    methods: {
      ifSelectALL() {
        // if (this.selectAll){
        //   this.shopCartList.forEach(item => item.checked = false)
        // }else{
        //   this.shopCartList.forEach(item => item.checked = true)
        // }
        if (this.selectAll) {
          this.$store.commit('AllProductDisChecked')
        } else {
          this.$store.commit('AllProductChecked')
        }

      },
      paying(){
       if (this.buyingCount === 0){
          this.$toast.changeShow('请选择一件商品！',2000)
       }
      }
    }
  }
</script>

