    border: 1px solid #DDDDDD;
  }

  .check {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    box-shadow: 0 0 2px rgba(0, 0, 0, .5);
  }

  .isActive {
    background-color: red;
  }

  .center {
    flex-grow: 1;
    font-size: 20px;
    text-align: center;
    line-height: 40px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    border: 1px solid #DDDDDD;
  }

  .center > :nth-child(1) {
    color: var(--color-tint);
